.academics {
    display: flex;
    flex-direction: column;
    align-items: center;
    background-image: url('../images/3.png');
    background-size: cover;
}

.academics-content {
    display: flex;
    height: 92vh;
}

.academics-text-right {
    text-align: right;
    width: 65%;
    margin: 1% 0 0 7.5%;
    display: flex;
    flex-direction: row;
    z-index: 1;
    align-items: center;
    justify-content: center;
}

.academics-text-left {
    text-align: left;
    width: 65%;
    margin: 0 0 0 1%;
    display: flex;
    flex-direction: row;
    z-index: 1;
    align-items: center;
    justify-content: center;
}

.academics-text>div {
    display: flex;
    flex-direction: column;
}

.btn-arrow {
    background-color: transparent;
    border: none;
    font-size: 5vw;
    z-index: 2;
}

.btn-arrow-left-acad {
    margin: 0 3.5% 0 2%;
}

.btn-arrow-right-acad {
    margin: 0 5% 0 3.5%;
}

.btn-arrow-next {
    width: 100%;
}

@media screen and (max-width: 910px) {
    .academics {
        height: 700px;
    }

    .academics-text-left {
        z-index: 1;
        width: 90%;
    }

    .btn-arrow-left-acad,
    .btn-arrow-right-acad {
        display: none;
    }
}